.descriptif-content{
    @include externalPaddingMobile;

    @include desktop {
      @include externalPaddingDesktop;
    }
}
.descriptif-content__banner{
    overflow: hidden;
    height: 255px;
    border-radius: 25px;
    @include desktop{
        height:415px;
    }
}
.description-content__center{
    width: 100%;
    height: auto;
    margin-top: 19px;
    margin-bottom: 50px;
  
    @include desktop {
      margin-top: 31px;
    }
}