.dropdown-container {
    margin-bottom: 33px;
}

.dropdown-content {
    position: relative;
    max-width: 1023px;
    margin: 0 auto;
}

.dropdown-btn {
    position: relative;
    width: 100%;
    height: 30px;
    padding: 0 11px;
    background-color: $primary-background-button;
    font-size: 13px;
    border: none;
    text-align: left;
    border-radius: 5px;
    cursor: pointer;
    color: $primary-font-button;

    @include desktop {
        height: 47px;
        padding: 0 18px;
        font-size: 24px;
    }
}

.dropdown-btn__arrowup {
    position: absolute;
    top: 9px;
    right: 18px;
    width: 17px;

    @include desktop {
        top: 15px;
        width: 30px;
        right: 11px;
    }
}

.rotate {
    transform: rotate(180deg);
}

.dropdown-list {
    display: block;
    width: 100%;
    margin-top: -5px;
    padding: 22px 0 19px 0;
    border-radius: 0 0 5px 5px;
    background-color: $primary-background-grey-light;

    & li {
        padding: 0 18px;
        font-size: 12px;
        color: $primary-color;
        font-weight: 400;
    }

    @include desktop {
        padding: 33px 0 19px 0;

        & li {
            font-size: 24px;
        }
    }
}